import React from 'react'
import { Button, Row, Col } from 'react-bootstrap'
import Fader from './Fader.js'
import NumberInput from './NumberInput.js'
import MSelect from './MSelect.js'
import Util from './util.js'
class DCSound extends React.Component {
  constructor(props) {
      super(props);
      this.state={csoundLoaded:"loading",csndstatus:'dbyellow',rvar:0,seed:5181359,pathseed:518041066,wave:0,scale:6,dist:0,dp:0.1, dmp:0.8};
      this.itm = { id : 'hvsamp', type : 'fader', label : 'amp', min: 0, max: 1, step: 0.001, defval: 0.6, height: 200 }
      this.itmrev = { id : 'hvsrev', type : 'fader', label : 'reverb', min: 0, max: 1, step: 0.01, defval: 0.6, height: 200 }
      this.itmdel = { id : 'hvsdel', type : 'fader', label : 'delay', min: 0, max: 1, step: 0.01, defval: 0.1, height: 200 }
      this.itmens = { id : 'hvsens', type : 'fader', label : 'ens', min: 0, max: 1, step: 0.01, defval: 0, height: 200 }
      this.itmfilt = { id : 'hvsfilt', type : 'fader', label : 'filter', log:true, min: 40, max: 15000, step: 0.01, defval: 5000, height: 200 }
      this.itmres = { id : 'hvsres', type : 'fader', label : 'res', min: 0, max: 1, step: 0.01, defval: 0.1, height: 200 }
      this.itmdrums = { id : 'drums', type : 'fader', label : 'drums', min: 0, max: 1, step: 0.01, defval: 0.4, height: 200 }
      this.niseed = { id : 'seed', type : 'numberinput', label : 'seed', size:"7", min: 0, max: 1000000, step: 1, defval: 0 }
      this.nipseed = { id : 'pathseed', type : 'numberinput', label : 'pseed', size:"7", min: 0, max: 100000, step: 1, defval: 0 }
      this.niwave = { id : 'wave', type : 'select', label : 'wave', defval: 0 , vertical:true,items:[
                                { name:'sine',value:0},
                                { name:'sin2',value:1},
                                { name:'saw',value:2},
                                { name:'pls',value:3},
                                { name:'tri',value:4},
                                { name:'rnd',value:5},
                                { name:'vcosaw',value:6},
                                { name:'vcopls',value:7},
                                { name:'vcotri',value:8},
                    ]}
      this.niscale = { id : 'scale', type : 'select', label : 'scales', defval: 0, vertical:true,
                       items: [
                                { name:'harmonic',value:0},
                                { name:'even',value:1},
                                { name:'odd',value:2},
                                { name:'limit',value:3},
                                { name:'majb7',value:4},
                                { name:'maj7-5+',value:5},
                                { name:'min-blue',value:6},
                                { name:'min-zo',value:7},
                                { name:'phry',value:8},
                              ]}
      this.rvar = { id : 'rvar', type : 'select', label : 'var', defval: 0, vertical:true,
                       items: [
                                { name:'var1',value:0},
                                { name:'var2',value:1},
                                { name:'rnd',value:2},
                              ]}

      this.dist = { id : 'dist', type : 'select', label : 'dist', defval: 0, vertical:true,
                       items: [
                                { name:'cauchy',value:0},
                                { name:'beta',value:1},
                                { name:'weibull',value:2},
                              ]}
      this.dp = { id : 'dp', type : 'fader', label : 'dens', min: 0.001, max: 0.2, step: 0.001, defval: 0.1 }
      this.dmp = { id : 'dmp', type : 'fader', label : 'dmp', min: 0.01, max: 1, step: 0.01, defval: 0.1 }

  }
  componentDidMount() {
    console.log(this.csound);
    setTimeout(() => {
      if(global.CsoundObj) {
      global.CsoundObj.initialize().then(() => {
      this.csound = new global.CsoundObj();
  //use this code to load a .csd file and have Csound compile and start it
      fetch('/vcohousegen_all.csd').then((response) => {
        response.arrayBuffer().then(async (buf) => {
        this.csound.writeToFS('vcohousegen_all.csd', buf);
        this.csound.compileCSD('vcohousegen_all.csd');
        this.csound.start();
        this.setState({csoundLoaded:"running"});
        this.setState({csndstatus:"dbgreen"});
        this.niseed.changeValue(821927);
        this.nipseed.changeValue(19331);
        this.niwave.changeValue(8)
        this.niscale.changeValue(6);
        this.rvar.changeValue(2);
        this.dmp.changeValue(0.78);
        this.dist.changeValue(1);
        this.dp.changeValue(0.08);
	      this.isPlaying = false;
	      this.isStarted = false;
        })
      })
      });
      } else {
        this.setState({csoundLoaded:"Loading failed (try to reload the page)"});
        this.setState({csndstatus:"dbred"});
      }
    },2000);
   }

  componentWillUnmount() {
     if(this.csound) {
        this.csound.stop();
        this.csound.destroy();
     }
  }

  handleStart() {
     if(!this.isPlaying) {
       if(!this.isStarted) {
          global.CsoundObj.CSOUND_AUDIO_CONTEXT.resume();
          this.started = true;
       }
       this.csound.readScore('i "trigDrums" 0 3000');
       this.csound.readScore(`i "hvs" 0.00067 3000 110 4 ${this.state.pathseed/100000} ${this.state.seed*2147.4836} ${this.state.wave} ${this.state.scale} ${this.state.rvar} ${this.state.dist} ${this.state.dp} ${this.state.dmp}`);
       this.isPlaying = true;
     }
  }
  handleRandom() {
       let seed = Util.randomIntM(1000000);
       this.niseed.changeValue(seed);
       let pathseed = Util.randomIntM(100000);
       this.nipseed.changeValue(pathseed);
       let wave = Util.randomIntM(9);
       this.niwave.changeValue(wave)
       let scale = Util.randomIntM(9);
       this.niscale.changeValue(scale);
       console.log(seed,pathseed,wave,scale);
  }
  handlePause() {
     if(this.started) {
       this.started = false;
       global.CsoundObj.CSOUND_AUDIO_CONTEXT.suspend();
     }
  }

  handleContinue() {
     if(!this.started) {
       this.started = true;
       global.CsoundObj.CSOUND_AUDIO_CONTEXT.resume();
     }
  }

  handleStop() {
     if(this.isPlaying) {
       this.csound.readScore('i "trigOff" 0 0.1');
       this.isPlaying = false;
     }
  }

  handleUpdate(id,val) {
    this.csound && this.csound.setControlChannel(id,val);
  }

  handleNI(id,value) {
    let st = {};
    st[id]=value;
    this.setState(st);
  }

  render() {
     const {
       state: { seed,pathseed,wave,scale,csoundLoaded },
     } = this
 
    return (
      <>
      <Row>
	      <Col><Button onClick={this.handleStart.bind(this)}>Start</Button></Col>
	      <Col><Button onClick={this.handleStop.bind(this)}>Stop</Button></Col>
	      <Col><Button onClick={this.handleRandom.bind(this)}>Random</Button></Col>
	      <Col><Button onClick={this.handlePause.bind(this)}>Pause</Button></Col>
	      <Col><Button onClick={this.handleContinue.bind(this)}>Continue</Button></Col>
      </Row>
      <Row><Col className={this.state.csndstatus}>Csound {csoundLoaded}</Col></Row>
      <Row><Col>stop and start after changing these parameters.</Col></Row>
      <Row noGutters="true">
        <Col>
          <Row><Col><NumberInput vertical={false} def={this.niseed} onChange={this.handleNI.bind(this)}/></Col></Row>
          <Row><Col><NumberInput vertical={false} def={this.nipseed} onChange={this.handleNI.bind(this)}/></Col></Row>
          <Row>
            <Col><MSelect vertical={true} def={this.rvar} onChange={this.handleNI.bind(this)}/></Col>
            <Col><MSelect vertical={true} def={this.dist} onChange={this.handleNI.bind(this)}/></Col>
          </Row>
        </Col>
        <Fader def={this.dp} key={this.dp.id} onChange={this.handleNI.bind(this)}/>
        <Col><MSelect vertical={true} def={this.niwave} onChange={this.handleNI.bind(this)}/></Col>
        <Col><MSelect def={this.niscale} onChange={this.handleNI.bind(this)} vertical={true}/></Col>
        <Fader def={this.dmp} key={this.dmp.id} onChange={this.handleNI.bind(this)}/>
      </Row>
      <Row><Col>performance parameters</Col></Row>
      <Row noGutters="true">
        <Fader def={this.itm} key={this.itm.id} onChange={this.handleUpdate.bind(this)}/>
        <Fader def={this.itmrev} key={this.itmrev.id} onChange={this.handleUpdate.bind(this)}/>
        <Fader def={this.itmdel} key={this.itmdel.id} onChange={this.handleUpdate.bind(this)}/>
        <Fader def={this.itmens} key={this.itmens.id} onChange={this.handleUpdate.bind(this)}/>
        <Fader def={this.itmfilt} key={this.itmfilt.id} onChange={this.handleUpdate.bind(this)}/>
        <Fader def={this.itmres} key={this.itmres.id} onChange={this.handleUpdate.bind(this)}/>
        <Fader def={this.itmdrums} key={this.itmdrums.id} onChange={this.handleUpdate.bind(this)}/>
      </Row>
      </>
    );

  }
}

export default DCSound
